    width: 100%;}
}


.firstSection {
  background-image: url(/static/media/Bajovita-first-bg.6907330d.jpg);
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.firstSection ul {
  background-color: #3b3b3bc0;
  text-align: center;
  width: 60%;
  font-size: 2.5rem;
  line-height: 6rem;
}

.firstSection ul li {
  color: #cc6600;
}

.firstSection ul li:hover {
  color: #ECECEC;
}

.firstSection ul li:hover i {
  color: #cc6600;
}

.firstSection ul i {
  color: #ECECEC;
}

.secondSection {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
  -ms-grid-rows: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 2vh;
}

.secondSection ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.prehliadka h2 {
  text-decoration: underline;
  color: #cc6600;
}

.prehliadka h2:hover {
  text-decoration: overline;
}

.socialNetworks {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.socialNetworks i {
  margin: 10px;
}

.socialNetworks i:hover {
  color: #cc6600;
  cursor: pointer;
}

.fixed-bg {
  background-image: url(/static/media/fixed-bg-coffe.50aee05b.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
  height: 40vh;
}

.projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 20vh 5vh 20vh 5vh;
}

.projects-group {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
  -ms-grid-rows: 1fr;
      grid-template-rows: 1fr;
  grid-gap: 15px;
  justify-content: center;
  align-items: center;
}

.projects-group ul {
  border: 2px #ECECEC solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  text-align: center;
  height: 100%;
}

.projects-group ul li img {
  width: 100%;
  height: 100%;
}

.reviews {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.reviews-group {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
  -ms-grid-rows: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
  grid-gap: 15px;
}

.reviews-group iframe {
  width: 500px;
  height: 200px;
  border: none;
  overflow: hidden;
}

@media screen and (max-width: 1030px) {
  .fixed-bg {
    display: none;
  }
  .reviews-group {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
  }
  .secondSection {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: 1fr 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    justify-content: center;
    align-items: center;
    -ms-grid-row-align: center;
    align-self: center;
    border-bottom: 2px solid #ECECEC;
    padding-top: 0vh;
    margin-top: 0vh;
  }
}

@media screen and (max-width: 800px) {
  .reviews {
    height: 100%;
  }
  .firstSection ul {
    width: 80%;
  }
}

@media screen and (max-width: 780px) {
  .projects-group {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 15px;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 420px) {
  .secondSection {
    -ms-grid-columns: 100%;
        grid-template-columns: 100%;
    justify-content: center;
    align-items: center;
  }
  .projects {
    height: 100%;
    margin: 0vh 0vh 0vh 0vh;
  }
  .reviews iframe {
    width: 100%;
  }
  .secondSection ul li {
    width: 100%;
  }
}
