.nav_links950 {
  display: none;
}

.burger {
  display: none;
}

nav {
  position: fixed;
  top: 0;
  background-color: #302f2fc9;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  height: 90px;
  width: 100%;
  color: white;
  z-index: 999;
}

nav li {
  font-weight: 400;
  font-size: 18px;
  color: #ECECEC;
}

nav ul {
  list-style: none;
}

.logo {
  width: 10rem;
  cursor: pointer;
}

.logo img {
  width: 100%;
}

.nav_links li {
  display: inline-block;
  padding: 0 25px;
}

.nav_links li {
  transition: all 0.3s  ease 0s;
}

.nav_links li:hover {
  color: #cc6600;
  text-decoration: overline;
}

@media screen and (max-width: 1030px) {
  nav {
    width: 100%;
    padding-left: 1vh;
    padding-right: 1vh;
  }
}

@media screen and (max-width: 950px) {
  .logo {
    z-index: 999;
    width: 8rem;
  }
  nav {
    width: 100%;
    justify-content: space-between;
    height: 10vh;
    padding-left: 5vh;
    padding-right: 5vh;
  }
  .burger {
    z-index: 10;
    display: block;
    font-size: 20px;
    cursor: pointer;
  }
  .nav_links {
    display: none;
  }
  .nav_links950 {
    z-index: 9;
    display: block;
    position: fixed;
    text-align: center !important;
    top: -2%;
    left: -1%;
    height: 100%;
    width: 100%;
    background-color: #363636;
    clip-path: circle(0vh);
    -webkit-clip-path: circle(0vh);
    transition: all 0.1s ease-out;
  }
  .nav_links950 li {
    margin: 10vh;
  }
  .open {
    transition: all 0.1s ease-out;
    clip-path: circle(100vh);
    -webkit-clip-path: circle(100vh);
  }
}

@media screen and (max-width: 420px) {
  .nav_links950 {
    top: -3%;
    left: -1%;
  }
}

@media screen and (max-width: 330px) {
  nav {
    padding-left: 10px;
    padding-right: 10px;
  }
  .burger {
    z-index: 10;
    display: block;
    font-size: 20px;
    cursor: pointer;
  }
  .nav_links {
    display: none;
  }
}
